<template>
  <div v-if="loading">
    <svg
      xmlns="http://www.w3.org/2000/svg"
      class="mx-auto animate-spin w-10 h-10 text-green-900"
      fill="none"
      viewBox="0 0 24 24"
      stroke="currentColor"
    >
      <path
        stroke-linecap="round"
        stroke-linejoin="round"
        stroke-width="2"
        d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15"
      />
    </svg>
  </div>
  <div v-else>
    <div
      class="
        flex flex-col
        md:flex-row
        p-5
        items-center
        space-y-2
        md:space-y-0 md:space-x-2
        text-gray-800 text-sm
      "
    >
      <div class="font-bold underline">Legende:</div>
      <div class="flex space-x-2">
        <div
          class="bg-yellow-600 h-5 w-5 border border-rttext-500 rounded"
        ></div>
        <div>Bestand bald leer</div>
      </div>
      <div class="flex space-x-2">
        <div
          class="bg-yellow-300 h-5 w-5 border border-rttext-500 rounded"
        ></div>
        <div>MHD läuft in weniger als 10 Tagen ab</div>
      </div>
      <div class="flex space-x-2">
        <div class="bg-red-400 h-5 w-5 border border-rttext-500 rounded"></div>
        <div>Beides</div>
      </div>
    </div>
    <table class="table-auto text-rttext-500 bg-gray-50 w-full">
      <thead class="bg-rttext-500 text-white text-left">
        <th class="border-b border-white p-4 hidden md:table-cell">
          Produkt-Code
        </th>
        <th class="border-b border-white p-4">Produktname</th>
        <th class="border-b border-white p-4">Anzahl/Menge</th>
        <th class="border-b border-white p-4 hidden md:table-cell">MHD</th>
      </thead>
      <tbody>
        <tr
          :class="{
            'bg-red-400':
              itemSoonOutOfStock(item) &&
              (itemSoonNotGood(item.good_till) ||
                itemSoonNotGood(item.old_good_till)),
            'bg-yellow-600':
              itemSoonOutOfStock(item) &&
              !(
                itemSoonNotGood(item.good_till) ||
                itemSoonNotGood(item.old_good_till)
              ),
            'bg-yellow-300':
              !itemSoonOutOfStock(item) &&
              (itemSoonNotGood(item.good_till) ||
                itemSoonNotGood(item.old_good_till)),
            'text-white':
              itemSoonOutOfStock(item) ||
              itemSoonNotGood(item.good_till) ||
              itemSoonNotGood(item.old_good_till),
          }"
          class="hover:bg-rttext-500 hover:text-white cursor-pointer"
          v-for="item in items"
          :key="item.id"
          @click="selectItem(item.product_code)"
        >
          <td class="border-b border-green-600 p-2 hidden md:table-cell">
            {{ item.product_code }}
          </td>
          <td class="border-b border-green-600 p-2">{{ item.product_name }}</td>
          <td class="border-b border-green-600 p-2 text-right">
            {{ item.is_gram ? item.amount + 'g' : item.amount + '' }}
          </td>
          <td
            class="
              border-b border-green-600
              p-2
              text-right
              hidden
              md:table-cell
            "
          >
            {{
              item.good_till === '1992-10-30'
                ? '-'
                : dayjs(item.good_till).format('DD.MM.YYYY')
            }}
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script lang="ts">
import { ApiRequest, API_URLS } from '@/api/request.api';
import { defineComponent, ref } from 'vue';
import dayjs from 'dayjs';
import { useRouter } from 'vue-router';

export default defineComponent({
  name: 'Lager',
  setup() {
    const loading = ref(true);
    const items = ref([]);
    const router = useRouter();

    const itemSoonNotGood = (goodTill: string) => {
      if (goodTill === '1992-10-30') return false;
      return dayjs(new Date()).isAfter(dayjs(goodTill).subtract(10, 'day'));
    };

    const itemSoonOutOfStock = (item: any) => {
      return item.is_gram
        ? item.amount + item.old_amount <= 1000
        : item.amount + item.old_amount <= 3;
    };

    const selectItem = (productCode: string) => {
      router.push({ name: 'Item', params: { productCode } });
    };

    new ApiRequest(API_URLS.SEARCH_ITEM)
      .get()
      .execute()
      .then((req) => {
        items.value = req.data;
      })
      .catch((err) => {
        throw new Error(err);
      })
      .finally(() => {
        loading.value = false;
      });
    return {
      loading,
      items,
      dayjs,
      selectItem,
      itemSoonNotGood,
      itemSoonOutOfStock,
    };
  },
});
</script>
